import { Component, OnInit } from '@angular/core';
import { SizeService } from '../../../shared/size.service';
@Component({
  selector: 'app-left-toolbar',
  templateUrl: './left-toolbar.component.html',
  styleUrls: ['./left-toolbar.component.css']
})
export class LeftToolbarComponent implements OnInit {
  public smartphone = true;
  public tab = true;
  public lap = false;
  public des = true;
  constructor(private size: SizeService) { }

  ngOnInit() {

  }
  phone() {
    let data = {
      "y": '914px',
      "x": '425px',
      'state': 'true'
    }
    this.smartphone= false;
    this.tab= true;
    this.lap= true;
    this.des= true;
    this.size.iframeSize(data)
  }
  tablet() {
    let data = {
      "y": '914px',
      "x": '768px',
      'state': 'true'
    }
    this.size.iframeSize(data)
    this.smartphone= true;
    this.tab= false;
    this.lap= true;
    this.des= true;
  }
  laptop() {
    let data = {
      "x": '85%',
      "y": '99.1%',
      'state': 'true'
    }
    this.size.iframeSize(data)
    this.smartphone= true;
    this.tab= true;
    this.lap= false;
    this.des= true;
  }
  dextop() {
    let data = {
      "x": '94%',
      "y": '942px',
      'state': 'true'
    }
    this.size.iframeSize(data)
    this.smartphone= true;
    this.tab= true;
    this.lap= true;
    this.des= false;
  }
}
